import React from 'react';
import ReactDOM from 'react-dom';
import './index.css';
import App from './App';
import 应用容器 from './我的组件/应用容器'
import 应用 from './我的组件/应用'
import 设置app from './我的组件/App/设置app'
import 设置通用列表 from './我的组件/设置/通用列表'
import 设置之我的手机 from './我的组件/App/设置之我的手机'
import 运营商网络 from './我的组件/App/设置之运营商网络'
import 无线网络 from './我的组件/App/设置之无线网络'
import 网络共享 from './我的组件/App/设置之网络共享'
import 蓝牙 from './我的组件/App/设置之蓝牙'
import reportWebVitals from './reportWebVitals';
import {BrowserRouter, Routes, Route} from 'react-router-dom';
import ScrollToTop from "./我的组件/ScrollToTop";


ReactDOM.render(
	<React.StrictMode>
		<BrowserRouter>
			<ScrollToTop info="跳转后页面回到顶部，不需要则注释" />
			<Routes>
				<Route path='/meizu' element={<App />} />
				<Route path='/meizu/run' element={<应用容器 />}>
					<Route path={encodeURI('设置')} element={<设置app />} />
					<Route path={encodeURI('设置/我的手机')} element={<设置之我的手机 />} />
					<Route path={encodeURI('设置/运营商网络')} element={<运营商网络 />} />
					<Route path={encodeURI('设置/无线网络')} element={<无线网络 />} />
					<Route path={encodeURI('设置/网络共享')} element={<网络共享 />} />
					<Route path={encodeURI('设置/蓝牙')} element={<蓝牙 />} />
					<Route path={encodeURI('设置/:item_name')} element={<设置通用列表 />} />
					<Route path=':app_name' element={<应用 />} />
					<Route path="*" element={
						<main style={{padding: "1em", margin: "1em", border: "solid"}}>
							开发中
						</main>
					} />
				</Route>
			</Routes>
		</BrowserRouter>
	</React.StrictMode>
	,
	document.getElementById('root')
);


// If you want to start measuring performance in your app, pass a function
// to log results (for example: reportWebVitals(console.log))
// or send to an analytics endpoint. Learn more: https://bit.ly/CRA-vitals
reportWebVitals();
