import 桌面1 from './桌面1'
import appData from '../datasource/应用程序.json'


export default function 桌面0() {
	return (
		<div style={{position: 'relative'}}>

			{/*上*/}
			<div style={{
				position: 'absolute', height: '40vh', left: 0, right: 0
			}}>
				上
			</div>

			{/*中*/}
			<div style={{
				position: 'absolute',
				height: '35vh',
				top: '40vh',
				left: 0,
				right: 0,
				padding: '0 1em'
			}}>
				<桌面1 data={appData.deskApps} />
			</div>

			{/*下*/}
			<div style={{
				position: 'absolute',
				height: '15vh',
				top: '85vh',
				left: 0,
				right: 0,
				padding: '0 1em'
			}}>
				<桌面1 data={appData.dockApps} />
			</div>

		</div>
	)
}