import React from 'react'
import List from '@mui/material/List';
import 设置数据 from '../../datasource/设置.data'
import 设置项 from './设置项'



export default function 设置之运营商网络() {
	console.log(设置数据)

	let data = 设置数据.asEnumerable().where(i => i.name === "运营商网络").first()
	console.log(data)

	let listData = data.children.asEnumerable().toArray()
	console.log(listData)


	let html = (
		<div id="setting-app">
			<h1>运营商网络</h1>
			<List>
				{
					listData.map((one, i) => (
						<设置项 data={one} index={i} key={`setting-item-${i}`} />
					))
				}
			</List>
		</div>
	)


	return html
}