import React from 'react'
import List from '@mui/material/List';
import 设置数据 from '../../datasource/设置.data'
import 设置项 from './设置项'



export default function 设置之网络共享() {
	console.log(设置数据)

	let data = 设置数据.asEnumerable().where(i => i.name === "网络共享").first()
	console.log(data)

	let listData = data.children.asEnumerable().toArray()
	console.log(listData)



	let html = (
		<div id="setting-app">
			<h1 style={{marginBottom: "0.2em"}}>网络共享 </h1>
			<sub style={{display: "inline-block", marginBottom: "1.5em"}}>
				开启热点共享，可再本页面通过 NFC 触碰实现免密共享网络
			</sub>
			<List>
				{
					listData.map((one, i) => (
						<设置项 data={one} index={i} key={`setting-item-${i}`} />
					))
				}
			</List>
		</div>
	)


	return html
}



