import React from 'react'
import Box from '@mui/material/Box'
import TextField from '@mui/material/TextField';
import SearchIcon from '@mui/icons-material/Search';
import Avatar from '@mui/material/Avatar';
import List from '@mui/material/List';
import ListItemButton from '@mui/material/ListItemButton';
import 设置数据 from '../../datasource/设置.data'
import 设置项 from './设置项'
import {useNavigate} from 'react-router-dom'





export default function 设置app(props) {
	console.clear()

	let navigate = useNavigate()

	const 呈现的项目List = []
	设置数据.asEnumerable().forEach((one, i) => {
		呈现的项目List.push(<设置项 data={one} padding={"0.1em 0"} key={`app-item-${i}`} />)
	})


	return (
		<Box sx={{padding: '1em'}}>

			<h1 style={{borderBottom: 'solid 1px'}}>
				设置
			</h1>


			{/*搜索框*/}
			<Box sx={{display: 'flex', alignItems: 'flex-end'}}>
				<SearchIcon sx={{color: 'action.active', mr: 1, my: 0.5}} />
				<TextField id="input-with-sx" label="搜索" variant="standard" sx={{width: '100%'}} />
			</Box>


			{/*项目*/}
			<List>
				<ListItemButton>
					{/*头像*/}
					<Avatar src="../resource/20220316215743Aicy.png" sx={{
						width: '3em',
						height: '3em',
						marginLeft: '-0.5em'
					}} />
					<div style={{margin: '1em'}}>
						<h3 style={{margin: 0}}>提包哥</h3>
						<label>账号信息、云服务和查找手机</label>
					</div>
				</ListItemButton>


				{
					呈现的项目List
				}

			</List>

		</Box>
	)
}